.border {
  position: relative;

  div {
    position: absolute;
    --light: 0.5;
    --dark: 0.15;
    --width: 5px;
    z-index: 2;

    &:nth-child(1) {
      height: var(--width);
      width: 100%;
      background-color: rgba(255, 255, 255, var(--light));
      // box-shadow: 0 0 20px 3px rgba(255, 255, 255, 0.33);
    }

    &:nth-child(2) {
      width: var(--width);
      height: 100%;
      background-color: rgba(0, 0, 0, var(--dark));
      top: 0;
      left: calc(100% - var(--width));
    }

    &:nth-child(3) {
      width: var(--width);
      height: 100%;
      background-color: rgba(255, 255, 255, var(--light));
      // box-shadow: 0 0 20px 3px rgba(255, 255, 255, 0.33);
    }

    &:nth-child(4) {
      height: var(--width);
      width: 100%;
      background-color: rgba(0, 0, 0, var(--dark));
      top: calc(100% - var(--width));
    }
  }
}

.active.border div, .active .border div {
  --light: 0.7;
}
