aside {
  background-color: #153153f2;
  user-select: none;
  width: 375px;
  height: calc(100% - 80px - 20px);
  box-shadow: 0 0 20px rgba(0, 0, 0, 0.5);
  outline: 5px solid rgba(0, 0, 0, 0.25);
  position: relative;
  z-index: 1;
  position: fixed;
  backdrop-filter: blur(17px);
  border-right: 5px solid rgba(255, 255, 255, 0.1);
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  padding-bottom: 20px;
  overflow: hidden;

  content {
    height: calc(100% - 87px);
    display: flex;
    width: calc(375px * 5);
    transition: transform 0.5s;
  }

  content > div {
    height: calc(100% - 0px);
    width: 100%;

    .hidor {
      margin: 0 25px;
      overflow: hidden;
      padding-top: 32px;
    }
  }

  header {
    ul {
      display: flex;
      list-style: none;
      padding: 5px 10px;
      justify-content: space-evenly;

      li {
        color: #8b96a8;
        padding: 7px 20px 2px;

        &.active {
          background-color: rgba(0, 0, 0, 0.75);
        }

        > * {
          pointer-events: none;
        }
      }
    }

    > div {
      margin: 0 32px;
      text-align: center;
      overflow: hidden;

      h2 {
        color: rgba(255, 255, 255, 0.75);
        font-size: 16px;
        display: inline-flex;
        position: relative;

        &:before {
          content: "";
          height: 5px;
          width: 150%;
          left: calc(100% + 20px);
          top: 33%;
          position: absolute;
          background-color: rgba(255, 255, 255, 0.25);
        }

        &:after {
          content: "";
          height: 5px;
          width: 150%;
          left: -20px;
          top: 33%;
          position: absolute;
          background-color: rgba(255, 255, 255, 0.25);
          transform: rotateY(180deg);
          transform-origin: left;
        }
      }
    }
  }
}
