button {
  padding: 10px;
  border: none;
  background: none;
  background-color: #cbcbcb;
  font-size: 20px;
  position: relative;
}

.active {
  button {
    background-color: #70b35b;
    box-shadow: 5px 5px 0 0 rgba(0, 0, 0, 0.25);

    p {
      color: #fff;
    }


    &:before {
      content: "";
      position: absolute;
      top: 0;
      left: 0;
      height: 100%;
      width: 100%;
      box-shadow: -3px -3px 7px 0px rgba(255, 255, 255, 0.33);
    }
  }
}
