#aside_claimlist {
  textarea {
    font-size: 8px;
    height: calc(100% - 30px);
    width: calc(100% - 30px);
    border: 10px solid rgba(255, 255, 255, 0.25);

    &:focus,
    &:active {
      background-color: rgba(0, 0, 0, 0.25);
      outline: none;
    }
  }

  p {
    font-size: 14px;
    line-height: 25px;
    color: #454543;
    filter: drop-shadow(0 2px 0 rgba(0, 0, 0, 0.1));
    font-family: MCFFR;
    padding: 20px;
    user-select: text;

    span {
      display: block;
      color: black;
      filter: drop-shadow(0 2px 0 rgba(0, 0, 0, 0));
      font-family: minecraftia;
      opacity: .33;
    }

    &:nth-child(5) {
      padding-bottom: 0;
    }

    &:nth-child(6) {
      padding-top: 0;
    }
  }

  .hidor {
    height: calc(100% - 64px);

    > div {
      // box-shadow: none;
      --light: 0.33;
    }

    > .border:first-child {
      height: 25%;
      margin-bottom: 20px;
      background-color: #cbcbcb;
        position: relative;
    }

    > .border:last-child {
      height: calc(75% - 20px);
    }
  }

  .border .border {
  position: absolute;
  right: 15px;
  bottom: 15px;

  div {

    --light: .2;
  }
  }

  button {
    font-size: 12px;
    background-color: #B54157;
    color:white;
    padding: 7px 10px 0 16px;
  }
}
