#selected_img {
  position: fixed;
  top: 80px;
  left: 0;
  height: calc(100% - 80px);
  width: 100%;
  display: none;
  z-index: 69;
  pointer-events: none;
  justify-content: center;
  align-items: center;
  background-color: rgba(0, 0, 0, 0.75);
  backdrop-filter: blur(20px);

  > div {
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;

    > * {
      pointer-events: all;
    }

    button {
      pointer-events: none;
      color: white;
      height: 60px;
      width: 60px;
      display: flex;
      justify-content: center;
      align-items: center;
      &.active {
        background-color: #70b35b;
        box-shadow: 5px 5px 0 0 rgba(0, 0, 0, 0.25);
        pointer-events: all;
      }

      img {
        height: 40px;
        width: 40px;
        image-rendering: pixelated;
      }
    }

    img {
      width: 1000px;
      margin: 0 20px;
    }
  }

  &.selected {
    pointer-events: all;
    display: flex;
  }
}
